// ÌÜµÌï© Ìó¨Ìçº Ìï®ÏàòÎì§
export const apiHelpers = {
  // Îß§Ìä∏Î¶≠Ïä§ ÌÇ§ ÏÉùÏÑ±
  generateMatrixKey: (type: 'criteria' | 'alternatives', criterionId?: string): string => {
    if (type === 'criteria') {
      return `C:${criterionId || 'root'}`;
    } else {
      return `A:${criterionId}`;
    }
  },

  // ÌÉÄÍ≤ü ÌÇ§ ÏÉùÏÑ± (ÏßÅÏ†ëÏûÖÎ†•Ïö©)
  generateTargetKey: (type: 'criterion' | 'alternative', itemId: string, criterionId?: string): string => {
    if (type === 'criterion') {
      return `criterion:${itemId}`;
    } else {
      return `alternative:${itemId}@criterion:${criterionId}`;
    }
  },

  // ÏóêÎü¨ Î©îÏãúÏßÄ Ï∂îÏ∂ú
  extractErrorMessage: (response: APIResponse): string => {
    if (response.error) {
      return response.error;
    }
    return 'Unknown error occurred';
  },

  // ÏÑ±Í≥µ Î©îÏãúÏßÄ Ï∂îÏ∂ú
  extractSuccessMessage: (response: APIResponse): string => {
    if (response.message) {
      return response.message;
    }
    return 'Operation completed successfully';
  },
};

// apiClientÏùò Î©îÏÑúÎìúÎì§ÏùÑ ÏßÅÏ†ë export
const apiService = {
  // HTTP Î©îÏÑúÎìúÎì§
  get: apiClient.get.bind(apiClient),
  post: apiClient.post.bind(apiClient),
  put: apiClient.put.bind(apiClient),
  delete: apiClient.delete.bind(apiClient),
  patch: apiClient.patch.bind(apiClient),
  
  // API Î™®ÎìàÎì§
  authAPI,
  directEvaluationAPI,
  pairwiseEvaluationAPI,
  ahpCalculationAPI,
  evaluatorAPI,
  resultsAPI,
  projectAPI,
  criteriaAPI,
  alternativesAPI,
  apiHelpers,
};

export default apiService;